import back from '@/components/back'
export default {
    components:{
        back
    },
	data(){
		return{
			 demo06_list: [],
			 demo06_index: 0,
			 imgArr:[],
			 info:"",
			 text:'',
			 showH:false,
			 price:'',
			 Pharmacodynamics:'',
			 Function:'',
			 defaultImg: 'this.src="' + require('../../assets/images/prescription-img.png') + '"'
		}	
	},
    created(){
       this.pull()
    },
    methods: {
        pull(){
           this.$post('Prescription/Medication/GetMedicationFullInfo',{
               Id: this.$route.params.id
           }).then(o => {
			   
			   this.imgArr = o.PicturePaths
			   this.info = o
			   if(o.Function){
			   		this.Function = this.info.Function.split('').length
			   }
			   console.log(this.Function)
			   let numberPrice = Number(this.info.Price).toString()
			   if(numberPrice.indexOf(".") == -1){
			   		this.price = this.info.Price+".00"
			   }else{
					this.price = this.info.Price
			   }
           })
		   
        },
		hideShow(){
			if(this.showH){
				this.showH = false
			}else{
				this.showH = true
			}
		},
    }
}



